





























































































































import {Component, Vue} from 'vue-property-decorator';

import BuilderNavbar from "@/components/BuilderUI/navbar/BuilderNavbar.vue";
import ShortcutMenu from "@/components/BuilderUI/shortcutmenu/ShortcutMenu.vue";
import ShortcutContent from "@/components/BuilderUI/shortcutmenu/ShortcutContent.vue";
import {Template} from "@/template/Template";
import {OMS} from "@/OMS";
import {Color, Position} from "@/BuefyUtils";
import {TemplateStorage} from "@/api/TemplateStorage";
import {FancyMessage} from "@/FancyMessage";
import {CRUD} from "@/api/CRUD";
import {Category, Request} from "@/api/URI";

@Component({
  name: "TemplateSettings",
  components: {ShortcutContent, ShortcutMenu, Navbar: BuilderNavbar},
})

export default class TemplateSettings extends Vue {
  get activeTemplate(): Template { return this.$store.state.template }

  isCalling = false;

  selectedMeta = {};
  selectedLink = {};

  beforeMount() {
    this.$store.state.template = OMS.parseTemplate(TemplateStorage.getTemplate());
  }

  onSave() {
    OMS.saveTemplate();
    this.isCalling = false;
    FancyMessage.sendToast('บันทึกแม่แบบแล้ว', 'is-success')
  }

  onDelete() {
    this.$buefy.dialog.confirm({
      title: `ยืนยันการลบแม่แบบ`,
      message: `<h4 class="title is-4">ลบแม่แบบ ${this.activeTemplate?.name}</h4>โดยการกระทำนี้จะทำการลบแม่แบบดังกล่าว คุณจะไม่สามารถนำแม่แบบกลับมาได้อีก</b>`,
      cancelText: 'กลับ',
      confirmText: `ยืนยันลบแม่แบบ ${this.activeTemplate?.name}`,
      type: 'is-danger',
      hasIcon: true,
      icon: 'trash-can',
      iconPack: 'mdi',
      onConfirm: async () => {
        this.isCalling = true;
        const crud = new CRUD(Category.TEMPLATE);
        crud.setRequest(Request.DELETE);
        crud.setAttachData(this.activeTemplate);
        await crud.shoot({});
        this.isCalling = crud.status;
        this.$router.push({name: "Builder@start"})
      }
    })
  }

  onChangePlaceholder() {
    OMS.showToast(
        "TEST",
        Color.SUCCESS,
        Position.TOP,
        3000
    );
  }

}

